
.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@media (max-width:768px){
  body{
    background-image: url("https://raw.githubusercontent.com/rajesh-govada/images/assets/hbdmob.jpg");
    background-repeat:no-repeat; 
    background-size: contain;
    background-position: center;
    color: #000000;  
    line-height: 500%;
    margin-top: 25%;
    text-align: center;
    font-family: "Monaco", monospace;
  };
}

@media (min-width:769px){
  body{
    background-image: url("https://raw.githubusercontent.com/rajesh-govada/images/assets/hbd.jpg");
    background-repeat:no-repeat;   
    background-size: cover;
    background-position: center; 
    color: #de392d; 
    line-height: 500%;
    font-family: "Monaco", monospace;
  };
}

.App-link {
  color: #cbdddc;
}